<template>

    <v-card flat tile width="100%">
        <v-footer>
            <v-card-subtitle>Powered by Nastya</v-card-subtitle>
            <v-card-title class="justify-center flex-grow-1">{{ cloak }}</v-card-title>
        </v-footer>
    </v-card>
</template>
<script>
    export default {
        name: "app-footer",
        data: () => ({
            cloak: "",
        }),
        methods: {
            cloakRun() {
                let date = new Date();
                let hours = date.getHours();
                let minutes = date.getMinutes();
                let seconds = date.getSeconds();
                let time = hours + ':' + minutes + ':' + seconds;

                return this.cloak = time;
            }
        },
        created() {
            return setInterval(() => this.cloakRun());
        },
    }
</script>