                <v-card class="h-full"  hover>
                  <v-card-actions :style="animationForOveralayCards"
                    :class="{'pa-2 pa-sm-3 pa-md-4': !hover, 'pa-0': hover}">
                    <v-img contain :src="item.cover" :width="resizerForImage" :height="resizerForImage" class="mx-auto">
                      <v-expand-transition>
                        <v-overlay absolute opacity=".7" color="info" :value="hover">
                          <div v-if="hover" class="d-flex transition-fast-in-fast-out" style="height: 100%;">
