                            </v-btn>
                          </div>
                        </v-overlay>
                      </v-expand-transition>
                    </v-img>
                  </v-card-actions>
                  <div 
                    class="d-inline-flex flex-column w-full"
                    :class="{'px-5': hover}"
                  >
                    <v-card-subtitle class="text-caption text-end font-italic py-0">{{ item.author }}</v-card-subtitle>
                    <v-card-text class="text-subtitle-2 font-weight-bold text-center py-2 text-non-space" v-if="$vuetify.breakpoint.xsOnly">
                      {{ item.title }}</v-card-text>
                    <v-card-title class="text-center justify-center pt-sm-1" v-else>{{ item.title }}</v-card-title>
                  </div>
                  <v-divider></v-divider>
                  <div
                      class="d-inline-flex w-full justify-end"
                    >
                      <v-tooltip
                          v-for="tip in booksTip"
                          :key="tip" 
                          bottom
                      >
                          <template  
                              v-slot:activator="{ on, attrs }"
                          >
                              <v-btn
                                  icon
                                  color="primary"
                                  size="19"
                                  v-bind="attrs"
                                  v-on="on"
                              >
                                  <v-icon
                                    :x-small="$vuetify.breakpoint.xsOnly"
                                  >mdi-{{tip.icon}}</v-icon>
                              </v-btn>
                          </template>
                          <span>{{ tip.text }}</span>
                      </v-tooltip>
                    </div>
                </v-card>
              </v-hover>
            </v-badge>
          </span>
        </div>
      </v-col>
    </v-row>
    <v-row
      justify-sm="center"
    >
      <v-col cols="12">
        <v-card
          v-click-outside="filterOptionsClickOutside"
          hover
        >
          <div class="d-inline-flex w-full pt-1 px-1">
            <v-btn
              class="align-self-center"
              icon
              tile
              @click="isOpenFilterOption = !isOpenFilterOption, isFilterOptionClickOutside = false"
            >
              <v-icon class="mx-auto">
                {{ isOpenFilterOption ? 'mdi-chevron-up' : 'mdi-chevron-down' }}
              </v-icon>
            </v-btn>
            <v-card-subtitle  
              class="mb-0 animate-selected pb-2 d-inline flex-grow-1 text-center text-sm-start"
            > 
              <span class="animation-fade">{{onFilterOptions[onFilterOptionModel].text}}</span>
            </v-card-subtitle>
            <v-btn
              icon
              tile
              class="justify-end align-self-center"
              v-show="onFilterOptionModel !== 0"
              @click="onFilterOptionModel = 0"
            >
              <v-icon
                color="error"
                class="mx-auto"
              >mdi-close</v-icon>
            </v-btn>
          </div>
          <v-divider></v-divider>
          <v-card-actions>
            <v-expand-transition>
              <v-list
                v-show="isOpenFilterOption && !isFilterOptionClickOutside"
                dense
                class="flex-grow-1"
              >
                <v-list-item-group 
                  v-model="onFilterOptionModel"
                  color="info"
                  @change="onFilterOptionsSwitch"
                >
                  <v-list-item
                      v-show="i !== 0"
                      v-slot="{ active, toggle }"
                      v-for="(item, i) in onFilterOptions" 
                      :key="item"
                      @click="isOpenFilterOption = false"
                    >
                      <v-list-item-icon>
                        <v-icon left>
                          mdi-{{ item.icon }}
                        </v-icon>
                      </v-list-item-icon>
                      <v-list-item-content>
                        <v-list-item-title>{{ item.text }}</v-list-item-title>
                      </v-list-item-content>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
            </v-expand-transition>
          </v-card-actions>
        </v-card>
      </v-col>
      <v-item-group>
        <v-item v-slot="{ toggle, active }" v-for="(item) in filtersForBooks" :key="item">
          <v-col cols="12" sm="10" class="mx-auto">
            <v-card class="d-flex flex-wrap justify-space-between" hover>
              <v-card-actions class="px-0 py-2 py-sm-3 py-md-4">
                <v-badge
                  v-show="!!item.badgeContent"
                  overlap
                  :content="item.badgeContent"
                  transition="slide-x-transition"
                >
                  <v-img contain :src="item.cover" :width="resizerForImage" :height="resizerForImage" class="mx-auto">
                  </v-img>
                </v-badge>
                <v-img contain :src="item.cover" :width="resizerForImage" :height="resizerForImage" class="mx-auto" v-show="item.badgeContent === undefined || item.badgeContent === ''">
                </v-img>
              </v-card-actions>
              <span style="flex-basis: 0;" class="d-inline-flex flex-column justify-center justify-sm-space-around flex-grow-1">
                <v-card-title class="px-1">{{item.title}}</v-card-title>
                <v-card-subtitle class="px-1 pb-3 text-end text-sm-center font-italic">- {{ item.author }}</v-card-subtitle>
                <p class="text-subtitle-2 text-sm-h6 mb-1 mb-sm-0">Цена: {{item.price}}p</p>
                <v-btn
                  text
                  outlined
                  v-show="$vuetify.breakpoint.xsOnly"
                >Посмотреть</v-btn>
              </span>
              <div class="w-full">
                <v-divider></v-divider>
                <span class="d-inline-flex w-full">
                  <v-menu
                    offset-y
                    bottom
                    open-on-hover
                    transition="scale-transition"
                  >
                    <template v-slot:activator="{value, on, attrs}">
                      <v-btn 
                        tile 
                        icon
                        v-on="on"
                        v-bind="attrs"
                      >
                        <v-icon class="justify-left d-inline-flex" style="transition: all .2s;">
                          {{ value ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
                      </v-btn>
                    </template>
                    <v-card-text class="white">{{item.description}}</v-card-text>
                  </v-menu>
                  <v-rating :value="item.rating" half-increments dense readonly class="d-inline-flex" size="19">
                  </v-rating>
                    <div
                      class="d-inline-flex flex-grow-1 justify-end"
                    >
                      <v-tooltip
                          v-for="tip in booksTip"
                          :key="tip" 
                          bottom
                      >
                          <template  
                              v-slot:activator="{ on, attrs }"
                          >
                              <v-btn
                                  icon
                                  color="primary"
                                  size="19"
                                  v-bind="attrs"
                                  v-on="on"
                              >
                                  <v-icon
                                    :x-small="$vuetify.breakpoint.xsOnly"
                                  >mdi-{{tip.icon}}</v-icon>
                              </v-btn>
                          </template>
                          <span>{{ tip.text }}</span>
                      </v-tooltip>
                    </div>
                </span>
              </div>
            </v-card>
          </v-col>
        </v-item>
      </v-item-group>
    </v-row>
  </v-container>
</template>
<script>
  import debounce from "lodash";
  export default {
    name: "Lib",
    props: {
      timeAnimate: {
        type: String,
        default: '.2',
        required: false
      }
    },
    data: () => ({
      isOpenFilterOption: false,
      isFilterOptionClickOutside: false,
      onFilterOptionModel: 0,
      onFilterOptionModelChange: false,
      onFilterOptions: [
      {
        text: "Выбрать опиции для фильтра",
      },
      {
        text: "Физическая копия",
        options: "physicalCopy",
        icon: "book-open-blank-variant"
      }, 
      {
        text: "Электронная копия",
        options: "onlineCopy",
        icon: "package-variant-closed"
      }, 
      {
        text: "От большего к меньшему",
        options: "higher",
        icon: "priority-high"
      }, 
      {
        text: "От меньшего к большему",
        options: "smaller",
        icon: "priority-low"
      }
    ],
    }),
    methods: {
      filterOptionsClickOutside() {
        return this.isFilterOptionClickOutside = true;
      },
      onFilterOptionsSwitch() {
        return this.onFilterOptionModelChange = true;
      }
    },
    computed: {
      animationFilterSwitch() {
        if(this.isOpenFilterOption) {
          return;
        }
        else {
          return this.onFilterOptionModelChange = !this.onFilterOptionModelChange; 
        }
      },
      animationForOveralayCards() {
        return {
          transition: `all ${this.timeAnimate}s`
        }
      },
      resizerForImage() {
        switch (this.$vuetify.breakpoint.name) {
          case 'xs':
            return 150
          case 'sm':
            return 200
        }
        return 200;
      },
      books() {
        return this.$store.state.books.item;
      },
      booksTip() {
        return this.$store.state.books.tips;
      },
      filtersForBooks() {
        let badgeContentAfterFiltered = "";
        let booksNotFiltred = this.$store.state.books.item;
        let filteredBooks = [];
        // for ignore vue cli err overlay delete on prod
        switch(this.onFilterOptionModel) {
          case 0: 
            filteredBooks = booksNotFiltred;
            break;
          case 1: 
            filteredBooks = booksNotFiltred.filter((item) => item.copy.physical);
            break;
          case 2: 
            filteredBooks = booksNotFiltred.filter((item) => item.copy.online);
            break;
          case 3:
            filteredBooks = booksNotFiltred.sort((a, b) => {
              return a.price - b.price;
            });
            break;
          case 4: 
            filteredBooks = booksNotFiltred.sort((a, b) => {
              return b.price - a.price;
            });
            break;
        }

        switch(this.onFilterOptionModel) {
          case 1: 
            badgeContentAfterFiltered = "Только физ. копия"; 
            break;
          case 2: 
            badgeContentAfterFiltered = "Только онлаин копия"; 
            break;
          case 3: 
            badgeContentAfterFiltered = "Самая дешевая";
            break;
          case 4: 
            badgeContentAfterFiltered = "Самая дорогая";
            break;
        }

        filteredBooks.forEach(item => item.badgeContent = "");
        filteredBooks.forEach((item, i) => {
          if(i === 0 && this.onFilterOptionModel === 3 || this.onFilterOptionModel === 4) {
            filteredBooks[0].badgeContent = badgeContentAfterFiltered;
          }
          else if(i !== 0 && this.onFilterOptionModel === 3 || this.onFilterOptionModel === 4) {
            item.badgeContent = "";
          }
          else {
            item.badgeContent = badgeContentAfterFiltered;
          }
        });
        return filteredBooks;
      },
      created() {
        let funcForDebounce = [this.filtersForBooks, this.resizerForImage, this.books];
        return funcForDebounce.forEach(item => {
          debounce(item, 7000);
        })
      }
    },
  }
</script>
