import Vue from 'vue'
import VueRouter from 'vue-router'


// import components for page
import Index from '@/views/Index.vue';
import Lib from '@/views/Lib.vue';
import Login from '@/views/Login.vue';
import User from '@/views/User.vue';
import Cart from "@/views/Market.vue";
import notFound from '@/views/NotFound.vue';

Vue.use(VueRouter)

// added icon prop for auto-make header tabs
const routes = [
  {
    path: '/notFound',
    name: notFound,
    component: notFound
  },
  {
    path: '/',
    name: 'Index',
    component: Index,
    alias: ["root"],
    meta: {
      isNavigateBar: true,
      icon: "slash-forward",
      text: "Главная"
    }
  },
  {
    path: '/lib',
    name: 'Library',
    component: Lib,
    alias: ["library", "book", "books", "Lib"],
    meta: {
      isNavigateBar: true,
      icon: "book-open-page-variant",
      text: "Библиотека"
    }
  }, 
  {
    path: '/login',
    name: 'login',
    component: Login,
  },
  {
    path: '/user',
    redirect: { path: '/login'}
  },
  {
    path: '/user/:userLogin/:userEmail',
    name: 'User',
    component: User,
    alias: ["cabinet", "user"],
    meta: {
      isNavigateBar: true,
      icon: "account",
      text: "Личный кабинет"
    }
  },
  {
    path: "/cart",
    name: "Cart",
    component: Cart
  }
];

const router = new VueRouter({
  routes
})

export default router
