<script>
    import {lastIndexOf} from "lodash";
    export default {
        name: "Cart",
        data: () => ({
            booksOnCart: [{title: "testCart", author: "testAutCart", cover: "https://www.tapeciarnia.pl/tapety/normalne/245937_kon_rzeka_laka.jpg"}, {title: "test1", author: "testAut1", cover: "https://www.tapeciarnia.pl/tapety/normalne/245937_kon_rzeka_laka.jpg"}],
            booksOnLike: [{title: "testLike", author: "testAutLike", cover: "https://www.tapeciarnia.pl/tapety/normalne/245937_kon_rzeka_laka.jpg"}, {title: "test1", author: "testAut1", cover: "https://www.tapeciarnia.pl/tapety/normalne/245937_kon_rzeka_laka.jpg"}],
            booksOnPurchased: [{title: "testPush", author: "testAutPush", cover: "https://www.tapeciarnia.pl/tapety/normalne/245937_kon_rzeka_laka.jpg"}, {title: "test1", author: "testAut1", cover: "https://www.tapeciarnia.pl/tapety/normalne/245937_kon_rzeka_laka.jpg"}],
            removedCartBooks: [],
            removedLikeBooks: [],
            removedPurchasedBooks: [],
            openRemovedCartMenu: false,
            openRemovedLikeMenu: false,
            openRemovedPurchasedMenu: false,
        }),
        methods: {
            removeCartItem(i) {
                this.removedCartBooks.push(this.booksOnCart[i]);
                return this.booksOnCart.splice(i, 1);
            },
            removeLikeItem(i) {
                this.removedLikeBooks.push(this.booksOnLike[i]);
                return this.booksOnLike.splice(i, 1);
            },
            removePurchasedItem(i) {
                this.removedPurchasedBooks.push(this.booksOnPurchased[i]);
                return this.booksOnPurchased.splice(i, 1); 
            },
            undoCartItem(i) {
                this.booksOnCart.push(this.removedCartBooks[i]);
                return this.removedCartBooks.splice(i, 1);
            },            
            undoLikeItem(i) {
                this.booksOnLike.push(this.removedLikeBooks[i]);
                return this.removedLikeBooks.splice(i, 1);
            },
            undoPurchasedItem(i) {
                this.booksOnPurchased.push(this.removedPurchasedBooks[i]);
                return this.removedPurchasedBooks.splice(i, 1); 
            },
        },
        computed: {
            getLastIndexOnCartBooks() {
                return lastIndexOf(this.booksOnCart);
            },
            getLastIndexOnLikeBooks() {
                return lastIndexOf(this.booksOnLike);
            },
            getLastIndexOnLikePurchasedBooks() {
                return lastIndexOf(this.booksOnPurchased);
            }
        }
    }
</script>